@font-face {
  font-family: 'Space Grotesk';
  src: url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@600&display=swap');;
}
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

code {
  font-family: 'Ubuntu', 'Space', 'Space Grotesk', 'SansSerif', 'Tahoma',
  monospace;
}

.App {
  margin: 0 auto;
  text-align: center;
  font-size: 220%;
}
.btnGroup > button {
  margin-top: 10%;
  cursor: pointer;
}
.btnGroup button:hover {
  background-color: #a47cec;
  box-shadow: 2px 3px 3px #201c29;
  transition: .5s;
}